___CSS_LOADER_EXPORT___.push([module.id, `body, html {
  background: #181c2f;
  color: #fff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.detail-navbar {
  background: #1e2a3a;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 40px;
}
.detail-navbar .logo {
  font-size: 1.4rem;
  color: #fff;
  display: flex;
  align-items: center;
}
.detail-navbar .logo-icon {
  font-size: 1.8rem;
  margin-right: 10px;
  color: #2a75bb;
}
.detail-navbar .nav-links {
  display: flex;
  gap: 40px;
  list-style: none;
}
.detail-navbar .nav-links li {
  color: white;
  font-weight: 500;
  cursor: pointer;
  font-size: 1rem;
  transition: color 0.2s;
  text-decoration: none;
}
.detail-navbar .nav-links li:hover {
  color: #2a75bb;
}
.detail-navbar .login-btn {
  background: #2a75bb;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 8px 20px;
  font-weight: 500;
  cursor: pointer;
  font-size: 0.95rem;
  transition: background 0.2s;
}
.detail-navbar .login-btn:hover {
  background: #1e4e7a;
}

.detail-container {
  max-width: 1200px;
  margin: 40px auto;
  background: #232946;
  border-radius: 18px;
  box-shadow: 0 4px 24px 0 rgba(0,0,0,0.18);
  padding: 40px;
}
.detail-header {
  display: flex;
  gap: 40px;
  align-items: flex-start;
}
.detail-cover {
  width: 320px;
  height: 420px;
  object-fit: cover;
  border-radius: 16px;
  background: #1a1a1a;
  box-shadow: 0 2px 16px 0 rgba(0,0,0,0.18);
}
.detail-main {
  flex: 1;
}
.detail-main h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 10px;
}
.genre-list {
  display: flex;
  gap: 10px;
  margin-bottom: 12px;
}
.genre-badge {
  background: #232946;
  color: #eebbc3;
  border-radius: 8px;
  padding: 4px 12px;
  font-size: 0.95rem;
  font-weight: 500;
  border: 1px solid #eebbc3;
}
.rating-match {
  display: flex;
  align-items: center;
  gap: 18px;
  margin-bottom: 18px;
}
.star {
  color: #ffd700;
  font-size: 1.3rem;
}
.rating {
  font-size: 1.1rem;
  font-weight: 600;
}
.match {
  background: #2a75bb;
  color: #fff;
  border-radius: 12px;
  padding: 2px 12px;
  font-size: 0.95rem;
  font-weight: 500;
}
.detail-actions {
  display: flex;
  gap: 16px;
  margin-bottom: 18px;
}
.trailer-btn, .koleksi-btn, .share-btn {
  background: #3a86ff;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 10px 18px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.18s;
  text-decoration: none;
  display: inline-block;
}
.trailer-btn:hover, .koleksi-btn:hover, .share-btn:hover {
  background: #265dbe;
}
.detail-tabs {
  display: flex;
  gap: 18px;
  margin: 32px 0 18px 0;
}
.tab-btn {
  background: #232946;
  color: #eebbc3;
  border: none;
  border-radius: 8px;
  padding: 8px 22px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.18s;
}
.tab-btn.active, .tab-btn:hover {
  background: #2a75bb;
  color: #fff;
}
.detail-content {
  display: flex;
  gap: 40px;
  margin-top: 18px;
}
.overview-section {
  flex: 2;
}
.info-box {
  background: #1a1a1a;
  border-radius: 12px;
  padding: 18px;
  margin-top: 18px;
  color: #b8c1ec;
  font-size: 1rem;
}
.info-box h3 {
  color: #fff;
  margin-bottom: 10px;
}
.feature-section {
  flex: 1;
  background: #1a1a1a;
  border-radius: 12px;
  padding: 18px;
  color: #b8c1ec;
  font-size: 1rem;
  margin-top: 0;
  height: fit-content;
}
.feature-section h3 {
  color: #fff;
  margin-bottom: 10px;
}
.feature-section ul {
  list-style: none;
  padding: 0;
}
.feature-section li {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}
.checkmark {
  color: #2a75bb;
  margin-right: 8px;
}
.screenshot-section {
  margin-top: 32px;
}
.screenshot-section h3 {
  color: #fff;
  margin-bottom: 10px;
}
.ss-list {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}
.ss-img {
  width: 180px;
  height: 100px;
  object-fit: cover;
  border-radius: 8px;
  background: #232946;
  box-shadow: 0 2px 8px 0 rgba(0,0,0,0.12);
}
.loading, .error {
  text-align: center;
  padding: 40px;
  color: #8a9ba8;
  font-size: 1.1rem;
}
.sysreq-section {
  background: #1a1a1a;
  width: 100%;
  border-radius: 12px;
  padding: 24px;
  color: #b8c1ec;
  margin-top: 12px;
  font-size: 1rem;
}
.sysreq-section h3 {
  color: #fff;
  margin-bottom: 10px;
}
.sysreq-box {
  background: #232946;
  border-radius: 8px;
  padding: 16px;
  color: #b8c1ec;
  font-size: 1rem;
  white-space: pre-line;
}
.review-section {
  background: #1a1a1a;
  border-radius: 12px;
  padding: 24px;
  color: #b8c1ec;
  margin-top: 12px;
  font-size: 1rem;
  width: 100%;
}
.review-section h3 {
  color: #fff;
  margin-bottom: 10px;
}
.review-box {
  background: #232946;
  border-radius: 8px;
  padding: 16px;
  color: #b8c1ec;
  font-size: 1rem;
  margin-bottom: 10px;
}
.dlc-section {
  background: #1a1a1a;
  border-radius: 12px;
  padding: 24px;
  color: #b8c1ec;
  margin-top: 12px;
  font-size: 1rem;
}
.dlc-section h3 {
  color: #fff;
  margin-bottom: 10px;
}
@media (max-width: 900px) {
  .detail-header, .detail-content {
    flex-direction: column;
    gap: 24px;
  }
  .detail-cover {
    width: 100%;
    height: 220px;
  }
  .detail-container {
    padding: 18px;
  }
}

.lightbox-overlay {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(20, 22, 40, 0.92);
  display: none;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}
.lightbox-content {
  position: relative;
  max-width: 90vw;
  max-height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
#lightbox-img {
  max-width: 80vw;
  max-height: 80vh;
  border-radius: 12px;
  box-shadow: 0 8px 32px 0 rgba(0,0,0,0.32);
  background: #232946;
}
.lightbox-close {
  position: absolute;
  top: -32px;
  right: 0;
  font-size: 2.5rem;
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  z-index: 10001;
  transition: color 0.2s;
}
.lightbox-close:hover {
  color: #eebbc3;
}

.recommend-section {
  background: #141c26;
  width: 100%;
  justify-content: center;
}

.recommend-section h3 {
  color: #fff;
  padding-top: 20px;
  padding-left: 20px;
}`, "",{"version":3,"sources":["webpack://./src/styles/deskripsi.css"],"names":[],"mappings":"AAAA;EACE,mBAAmB;EACnB,WAAW;EACX,4DAA4D;AAC9D;;AAEA;EACE,mBAAmB;EACnB,aAAa;EACb,mBAAmB;EACnB,8BAA8B;EAC9B,kBAAkB;AACpB;AACA;EACE,iBAAiB;EACjB,WAAW;EACX,aAAa;EACb,mBAAmB;AACrB;AACA;EACE,iBAAiB;EACjB,kBAAkB;EAClB,cAAc;AAChB;AACA;EACE,aAAa;EACb,SAAS;EACT,gBAAgB;AAClB;AACA;EACE,YAAY;EACZ,gBAAgB;EAChB,eAAe;EACf,eAAe;EACf,sBAAsB;EACtB,qBAAqB;AACvB;AACA;EACE,cAAc;AAChB;AACA;EACE,mBAAmB;EACnB,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,iBAAiB;EACjB,gBAAgB;EAChB,eAAe;EACf,kBAAkB;EAClB,2BAA2B;AAC7B;AACA;EACE,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;EACjB,iBAAiB;EACjB,mBAAmB;EACnB,mBAAmB;EACnB,yCAAyC;EACzC,aAAa;AACf;AACA;EACE,aAAa;EACb,SAAS;EACT,uBAAuB;AACzB;AACA;EACE,YAAY;EACZ,aAAa;EACb,iBAAiB;EACjB,mBAAmB;EACnB,mBAAmB;EACnB,yCAAyC;AAC3C;AACA;EACE,OAAO;AACT;AACA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,mBAAmB;AACrB;AACA;EACE,aAAa;EACb,SAAS;EACT,mBAAmB;AACrB;AACA;EACE,mBAAmB;EACnB,cAAc;EACd,kBAAkB;EAClB,iBAAiB;EACjB,kBAAkB;EAClB,gBAAgB;EAChB,yBAAyB;AAC3B;AACA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;EACT,mBAAmB;AACrB;AACA;EACE,cAAc;EACd,iBAAiB;AACnB;AACA;EACE,iBAAiB;EACjB,gBAAgB;AAClB;AACA;EACE,mBAAmB;EACnB,WAAW;EACX,mBAAmB;EACnB,iBAAiB;EACjB,kBAAkB;EAClB,gBAAgB;AAClB;AACA;EACE,aAAa;EACb,SAAS;EACT,mBAAmB;AACrB;AACA;EACE,mBAAmB;EACnB,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,kBAAkB;EAClB,eAAe;EACf,gBAAgB;EAChB,eAAe;EACf,4BAA4B;EAC5B,qBAAqB;EACrB,qBAAqB;AACvB;AACA;EACE,mBAAmB;AACrB;AACA;EACE,aAAa;EACb,SAAS;EACT,qBAAqB;AACvB;AACA;EACE,mBAAmB;EACnB,cAAc;EACd,YAAY;EACZ,kBAAkB;EAClB,iBAAiB;EACjB,eAAe;EACf,gBAAgB;EAChB,eAAe;EACf,4BAA4B;AAC9B;AACA;EACE,mBAAmB;EACnB,WAAW;AACb;AACA;EACE,aAAa;EACb,SAAS;EACT,gBAAgB;AAClB;AACA;EACE,OAAO;AACT;AACA;EACE,mBAAmB;EACnB,mBAAmB;EACnB,aAAa;EACb,gBAAgB;EAChB,cAAc;EACd,eAAe;AACjB;AACA;EACE,WAAW;EACX,mBAAmB;AACrB;AACA;EACE,OAAO;EACP,mBAAmB;EACnB,mBAAmB;EACnB,aAAa;EACb,cAAc;EACd,eAAe;EACf,aAAa;EACb,mBAAmB;AACrB;AACA;EACE,WAAW;EACX,mBAAmB;AACrB;AACA;EACE,gBAAgB;EAChB,UAAU;AACZ;AACA;EACE,mBAAmB;EACnB,aAAa;EA